.hcontact{
    color: rgb(25, 25, 102);
}
.left-info{
    background-color: #2a1d97;
    color: white;
    border-radius: 15px;
}
.main-contact{
    background-color: white;
}
.fillup{
    word-spacing: -1px;
    letter-spacing: 1px;
    color: wheat;
}
.textfield{
    width: 250px !important;
    margin: 18px !important;
}
.r-color{
    color:#2a1d97!important;
    font-weight: 700;
}
.MuiRadio-colorSecondary.Mui-checked {
    color: #2a1d97 !important;
}
.send-m{
    background-color: #2a1d97;
    color: white;
    padding:15px 40px 15px 40px;
    border-radius: 10px;
    font-size: 15px;
} 
.send-m:hover{
    color: white;  
}
.icons{
    margin-left:15px;
    font-size: medium;
    color: wheat;
}
.icondiv{
    color: rgb(186 109 186);
    font-size: large;
}
.quarter-circle {
    display: inline;
    margin: 0px !important;
    padding: 0px !important;
    width: 205px;
    height: 140px;
    border-bottom-right-radius: 10px;
    -webkit-clip-path: circle(50% at 83% 84%);
    clip-path: circle(67% at 87% 84%);
    background-color: rgb(186 109 186);
    position: relative;
    float: right;
    opacity: 0.5;
}
.circle {
    position: absolute;
    display: inline;
    height: 100px;
    width: 100px;
    background-color: rgb(210 43 210);
    -webkit-clip-path: circle(50% at 50% 50%);
    clip-path: circle(50% at 51% 50%);
    margin:-25px 0px 0px 300px;
    opacity: 1.5;
   
    
}
.footer-icons{
    margin-left:15px;
    font-size: small;
    border-radius: 50%
   
}

.footer-icons:hover{
    background-color: wheat;
   ;
   
    color: red;
}
.footer{
    width:20px;
    border-radius: 50%
}
